.addRecipte {
  width: 100%;
  padding: 30px 50px;
  background-image: url('../images/background.jpg');
  background-repeat: no-repeat;
  background-size: cover;

  &__form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__input {
    width: 70%;
    height: 40px;
    margin: 0 auto;
    margin-bottom: 10px;
    &--textarea {
      min-height: 100px;
    }
  }

  &__button {
    width: 50px;
    height: 40px;
    background-color: white;
    color: black;
    padding: 3px;
    margin: 0 auto;
    margin-top: 10px;
    transition: 0.3s;
    border: 1px solid black;
    border-radius: 10px;
    cursor: pointer;
    &:hover {
      background-color: black;
      color: white;
    }
  }

  &__counter {
    font-size: 36px;
    color: rgb(26, 255, 0);
    margin-top: 20px;
    width: 100%;
    font-style: italic;
    border-bottom: 1px solid rgb(26, 255, 0);
  }

  &__title {
    font-size: 48px;
    color: rgb(26, 255, 0);
    margin-bottom: 20px;
    width: 100%;
    font-style: italic;
  }
}
